<template>
  <v-card class="px-2">
    <v-card-title>
      <v-avatar size="120">
        <img
          alt="user"
          :src="
            person.avtUrl ||
              'data:image/png;base64,'
          "
        />
      </v-avatar>
      <p class="ml-6 font-weight-bold text-h4 my-auto">
        {{person.name||"Name"}}
        <v-btn
          color="green"
          dark
          elevation="2"
          fab
          x-small
          rounded
          @click="dialog = true"
        >
          <v-icon>mdi-pencil</v-icon>
        </v-btn>
        <v-dialog max-width="600px" v-model="dialog">
          <v-card>
            <v-app-bar dark color="primary">
              <h2>Cập nhật thông tin cá nhân</h2>
            </v-app-bar>
            <v-card-text>
              <v-container>
                <v-row>
                  <v-col cols="12">
                    <v-text-field
                      v-model="name"
                      label="Họ Và Tên"
                      required
                    ></v-text-field>
                  </v-col>
                  <v-col cols="12">
                    <v-text-field
                      v-model="email"
                      label="Email"
                      required
                    ></v-text-field>
                  </v-col>
                  <v-col cols="12">
                    <div>
                      <v-menu
                        ref="menu"
                        v-model="menu"
                        :close-on-content-click="false"
                        transition="scale-transition"
                        offset-y
                        min-width="auto"
                      >
                        <template v-slot:activator="{ on, attrs }">
                          <v-text-field
                            v-model="date"
                            label="Birthday date"
                            prepend-icon="mdi-calendar"
                            readonly
                            v-bind="attrs"
                            v-on="on"
                          ></v-text-field>
                        </template>
                        <v-date-picker
                          v-model="date"
                          :active-picker.sync="activePicker"
                          :max="
                            new Date(
                              Date.now() -
                                new Date().getTimezoneOffset() * 60000
                            )
                              .toISOString()
                              .substr(0, 10)
                          "
                          min="1950-01-01"
                          @change="save"
                        ></v-date-picker>
                      </v-menu>
                    </div>
                  </v-col>
                  <v-col>
                    <v-text-field
                      name="avt"
                      label="Avatar URL"
                      v-model="avtUrl"
                    ></v-text-field>
                  </v-col>
                  <v-col>
                    <v-avatar size="80" color="warning">
                      <img :src="avtUrl" />
                    </v-avatar>
                  </v-col>
                </v-row>
              </v-container>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="personUpdate(name, date, email, avtUrl);dialog = false;">
                Cập Nhật
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </p>
    </v-card-title>
    <v-divider></v-divider>
    <v-card-text>
      <div class="font-weight-bold ml-8 mb-2 text-align-center">
        Birthday: {{ person.dateOfBirth }} <br />
        Email: {{ person.email }} <br />
        {{ person.personAddress }}
      </div>

      <v-dialog transition="dialog-bottom-transition" max-width="1000">
        <template v-slot:activator="{ on, attrs }">
          <v-btn class="ml-8 mt-3" color="primary" v-bind="attrs" v-on="on"
            >My Certificate</v-btn
          >
        </template>
        <template v-slot:default>
          <v-card>
            <v-card-title>
              Danh Sách Chứng Nhận Của Tôi
              <v-spacer></v-spacer>
              <v-text-field
                v-model="search"
                append-icon="mdi-magnify"
                label="Search"
                single-line
                hide-details
              ></v-text-field>
            </v-card-title>
            <v-data-table
            :headers="headers"
            :items="person.cers"
            :search="search"
            height="450"
          >
            <template v-slot:item.date="{ item }">
              {{ time(item.date) }}
            </template>
            <template v-slot:item.action="{ item }">
              <v-dialog transition="dialog-bottom-transition" max-width="600">
                <template v-slot:activator="{ on, attrs }">
                  <v-btn color="primary" icon v-bind="attrs" v-on="on">
                    <v-icon>mdi-information</v-icon>
                  </v-btn>
                </template>
                <template v-slot:default>
                  <v-card>
                    <v-toolbar color="primary" dark
                      ><h2>Thông tin chi tiết</h2></v-toolbar
                    >
                    <v-card-text class="mt-3">
                      <v-container fluid>
                        <v-row>
                          <v-col cols="12"
                            ><h1 align="center">{{ item.name }}</h1>
                          </v-col>
                          <v-col cols="12"
                            ><h3 class="px-8" align="center">
                              {{ item.content }}
                            </h3>
                          </v-col>
                          <v-col cols="6">
                            <h3>From: {{ item.author }}</h3>
                            <h3>
                              To: <span class="address">{{ item.person }}</span>
                            </h3>
                          </v-col>
                          <v-divider vertical></v-divider>
                          <v-col cols="6" align="center">
                            <h4 align="center">{{ time(item.date) }}</h4>
                          </v-col>
                        </v-row>
                        <v-row>
                          <v-col cols="6" align="center">
                            <h4 align="center" class="mt-2">Mã Băm Nội Dung</h4>
                            <span align="center">
                              <img :src="'http://api.qrserver.com/v1/create-qr-code/?data='+contentHash(item.name, item.content, item.person)+'&size=150x150'"/>
                            </span>
                          </v-col>
                          <v-divider vertical></v-divider>
                          <v-col cols="6" align="center">
                            <h3 class="mt-2">Chữ Ký</h3>
                            <img :src="'http://api.qrserver.com/v1/create-qr-code/?data='+item.signature+'&size=150x150'"/>
                          </v-col>
                        </v-row>
                      </v-container>
                    </v-card-text>
                  </v-card>
                </template>
              </v-dialog>
            </template>
          </v-data-table>
          </v-card>
        </template>
      </v-dialog>
    </v-card-text>
  </v-card>
</template>

<script>
import {web3, contract } from "../web3/web3";
export default {
  data() {
    return {
      dialog: null,
      person: {},
      activePicker: null,
      date: null,
      menu: false,
      avtUrl: "",
      search: "",
      headers: [
        { text: "Chứng Nhận", value: "name" },
        { text: "Người Nhận", value: "person" },
        { text: "Thời Điểm Cấp", value: "date" },
        { text: "Chi Tiết", value: "action" },
      ],
      name:'',
      email:'',

    };
  },
  watch: {
    menu(val) {
      val && setTimeout(() => (this.activePicker = "YEAR"));
    },
  },
  methods: {
    contentHash(name, content, person) {
      return web3.utils.sha3(name + content + person);
    },
    time(stamp) {
      var m = new Date(stamp * 1000);
      return (
        m.getUTCFullYear() +
        "/" +
        (m.getUTCMonth() + 1) +
        "/" +
        m.getUTCDate() +
        " " +
        m.getUTCHours() +
        ":" +
        m.getUTCMinutes() +
        ":" +
        m.getUTCSeconds()
      );
    },
    personUpdate(name, date, email, avt) {
      contract.methods
        .personUpdate(name, date, email, avt)
        .send({ from: this.$store.state.address })
        .on("receipt", () => {
          contract.methods
            .getPerson()
            .call({ from: this.$store.state.address })
            .then((data) => {
              this.person = data;
              console.log(
                this.person.personAddress ==
                  "0x0000000000000000000000000000000000000000"
              );
              if (
                this.person.personAddress ==
                "0x0000000000000000000000000000000000000000"
              ) {
                console.log(this);
                this.dialog = true;
              }
            });
        });
    },
    save(date) {
      this.$refs.menu.save(date);
    },
  },
  created() {
    console.log(this.$store.state.address);
    window.ethereum
      .request({
        method: "eth_requestAccounts",
      })
      .then((data) => {
        this.$store.state.address = data[0];
        contract.methods
          .getPerson()
          .call({ from: this.$store.state.address })
          .then((data) => {
            this.person = data;
            console.log(
              this.person.personAddress ==
                "0x0000000000000000000000000000000000000000"
            );
            if (
              this.person.personAddress ==
              "0x0000000000000000000000000000000000000000"
            ) {
              console.log(this);
              this.dialog = true;
            }
          });
      });
  },
};
</script>

<style scoped>
.address {
  font-size: 9px;
  font-weight: 300;
}</style>
